import React, { Component } from 'react';

class Suggestion extends Component {
    constructor(props) {
        super(props);
        this.state = {  }
    }

    onItemClick(){
        console.log("hola");
        // this.props.onItemClick(e);
    }

    render() { 
        return ( 
            <ul className="list-group">
                {this.props.list.map( (v,i) => {
                    return(
                        <div key={i} onMouseDown={(e) => this.props.onItemClick(i)}>
                            <li className="list-group-item py-2">{v.name}</li>   
                        </div> 
                )}, this)}
            </ul>
        )
    }
}
 
export default Suggestion;


